import Vue from 'vue'
import axios from 'axios'
import {Toast} from 'vant';
import qs from 'qs';
import uerstore from './store'//
Vue.config.productionTip = false;
Vue.prototype.axios = axios; 
Vue.prototype.qs = qs 
//
axios.defaults.baseURL='https://yxhd.insooner.com/api/v1/'
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';

import wx from 'weixin-js-sdk'

export const shareTitle = '';
export const shareUrl = '';
export const shareImg = '';
export const shareDesc = '';

/**
 *
 * @param _this
 * @param shareTitle 
 * @param shareUrl 
 * @param shareImg 
 * @param shareDesc 
 */
export const commonShare = (shareTitle, shareUrl, shareImg, shareDesc) => {
  let url = window.location.href;	
  let data = {
    url:url
  };
  axios.post('share',qs.stringify(data)).then((res)=>{
	  console.log('',res);
  	// ...
  	if (res.status == 200) {
  	  let data = res.data;
  	  wx.config({
  	    debug: false, // ,apialertpclogpc
  	    appId: data.appId, // 
  	    timestamp: data.timestamp, // 
  	    nonceStr: data.nonceStr, // 
  	    signature: data.signature, // 1
  	    jsApiList: ["onMenuShareTimeline", "onMenuShareAppMessage"] // JSJS2
  	  });
  	  wx.ready(function () {
  	    wx.onMenuShareTimeline({
  	      title: shareTitle, // 
  	      link: shareUrl, // JS
  	      imgUrl: shareImg, // 
  	      success: function () {
  	        // 
  	        Toast('');
  	      },
  	      cancel: function () {
  	        // 
  	        Toast('');
  	      }
  	    });
  	    wx.onMenuShareAppMessage({
  	      title: shareTitle, // 
  	      desc: shareDesc, // 
  	      link: shareUrl, // JS
  	      imgUrl: shareImg, // 
  	      type: "", // ,musicvideolinklink
  	      dataUrl: "", // typemusicvideo
  	      success: function () {
  	        // 
  	        Toast('');
  	      },
  	      cancel: function () {
  	        // 
  	        Toast('');
  	      }
  	    });
  	  });
  	}else{
		Toast('');
	}
  })
};



