<template>
	<div class="inOrder">
		<div class="inOrderOne">
			<div class="inOrderOneTIT" v-for="(item,index) in JGlist" :key="index">{{item.cname}}<span>{{item.price}}</span></div>
			<div class="inOrderOnemud"><span>{{JGlist.length}}</span></div>
		</div>
		<div class="inOrderwto" v-show="informationXGstate==2?true:false">
			<div class="inOrderwtoqx" v-show="uidstate!=2?true:false"><span @click="informationXG(1)"></span></div>
			<van-field v-model="INname" label="" type="textarea" placeholder="" rows="1" autosize />
			<div style="padding-left: 16px;">
				<van-radio-group v-model="radio" class="INbox">
					<van-radio checked-color="#FF6714" name="1" class="boxxz" style="margin-left: 25%;"></van-radio>
					<van-radio checked-color="#FF6714" name="2" class="boxxz"></van-radio>
				</van-radio-group>
			</div>
			<van-field v-model="INage" label="" type="tel" placeholder="" rows="1" autosize />
			<van-field v-model="INtel"  center  clearable label="" placeholder="">
			    <van-button v-show="show" slot="button" style="background-color:#FF6714;border:none;" size="small" type="primary" @click="oncode()"></van-button>
				<van-button v-show="!show" slot="button" style="background-color:#FF6714;border:none;" size="small" type="primary">{{count}}</van-button>
			</van-field>
			<van-field v-model="INcode" label="" maxlength="4" type="tel" placeholder="" rows="1" autosize />
		</div>
		<div class="inOrderthree" v-show="informationXGstate==1?true:false">
			<div><span>{{urexinxi.name}}</span><span class="inOrderthreeright">{{urexinxi.sex==1?'':''}}</span></div>
			<div><span>{{urexinxi.phone}}</span><span class="inOrderthreeright">{{urexinxi.age}}</span></div>
			<p class="inOrderthreebut" @click="informationXG(2)"></p>
		</div>
		<div class="inOrderBottom">
			<p @click="INbmok()"></p>
		</div>
	</div>
</template>

<script>
	import {
		Toast,Dialog 
	} from 'vant';
	export default {
		data() {
			return {
				INname: '',
				INage: '',
				INtel: '',
				radio: '1',
				INcode:'',
				JGlist:[],
			    show: true,
			    count: '',
			    timer: null,
				informationXGstate:2,//21
				uidstate:2,//
				urexinxi:[],//
				TJstate:2,//
			};
		},
		methods: {
			//
			informationXG(e){
				this.informationXGstate=e;
				this.TJstate = e;
			},
			//
			oncode(){
				if(!(/^1[3456789]\d{9}$/.test(this.INtel))){
					Toast("");  
					return false; 
				}else{
					//
					const TIME_COUNT = 60;
					 if (!this.timer) {
					   this.count = TIME_COUNT;
					   this.show = false;
					   this.timer = setInterval(() => {
					   if (this.count > 0 && this.count <= TIME_COUNT) {
						 this.count--;
						} else {
						 this.show = true;
						 clearInterval(this.timer);
						 this.timer = null;
						}
					   }, 1000)
					  }	
					this.codeajax();
				}
			},
			INbmok() {
				if (this.TJstate==1) {
					let ajaxlistcon={
						uid:localStorage.getItem('INcodeuid'),
						ids:this.$route.query.id,
						catType:this.$route.query.catType,
						type_Sub:this.TJstate
					};
					if (this.JGlist.length==6) {
						 this.JGtijiao(ajaxlistcon);
					} else{
						Dialog.confirm({
						  title: '',
						  message: ''+this.JGlist.length +''
						}).then(() => {
						  // on confirm
						 this.JGtijiao(ajaxlistcon);
						}).catch(() => {
						  Toast(""); 
						});
					}
				}else{
					let ajaxlistcon={
						uid:localStorage.getItem('INcodeuid'),
						ids:this.$route.query.id,
						catType:this.$route.query.catType,
						sname:this.INname,
						sex:this.radio,
						age:this.INage,
						phone:this.INtel,
						code:this.INcode,
						type_Sub:this.TJstate
					};
					//
					if (ajaxlistcon.sname=='') {
						return Toast("");  
					};
					if (ajaxlistcon.age==''|| isNaN(ajaxlistcon.age)) {
						return Toast("6");  
					};
					if (ajaxlistcon.phone=='') {
						return Toast("");  
					};
					if (ajaxlistcon.code=='') {
						return Toast("");  
					};
					//
					if(!(/^1[3456789]\d{9}$/.test(this.INtel))){
						return Toast("");  	
					};
					if (this.JGlist.length==6) {
						 this.JGtijiao(ajaxlistcon);
					} else{
						Dialog.confirm({
						  title: '',
						  message: ''+this.JGlist.length +''
						}).then(() => {
						  // on confirm
						 this.JGtijiao(ajaxlistcon);
						}).catch(() => {
						  Toast(""); 
						});
					}					
				}	
			},
			//
			JGtijiao(ajaxlistcon){
				Toast.loading({
				  message: '...',
				  forbidClick: true
				});
				this.axios.post('addOrder',this.qs.stringify(ajaxlistcon)).then((response)=>{
				    // ...
					console.log('',response.data);
					Toast(response.data.msg);
					if (response.data.code==1000) {
						// this.$router.push({
						// 	path: '/inSuccess',
						// 	query: {
						// 		id: response.data.data.order_num,
						// 		mud:this.JGlist.length
						// 	}
						// });
						Toast.clear();
						this.$uerstore.store.commit("SuccessBAOM",99);//
						this.$router.push({path:'/'});
					};
				});
			},
			//
			JGajaxlist(){
				let dataS={
					ids:this.$route.query.id,
					id:this.$route.query.uid,
				};
				this.axios.post('getMemberData',this.qs.stringify(dataS)).then((response)=>{
				    // ...
					console.log('',response.data);
					this.JGlist = response.data.data.Member_data;
					this.uidstate = response.data.data.name_data.type;//
					this.informationXGstate = response.data.data.name_data.type;//
					this.TJstate = response.data.data.name_data.type;//
					this.urexinxi = response.data.data.name_data;//
				})
			},
			//
			codeajax(){
				let codees={
					phone:this.INtel
				};
				this.axios.post('alimsg',this.qs.stringify(codees)).then((response)=>{
				    // ...		
					Toast(""); 			
				})
			},
		},
		watch:{
			INname(e){
				localStorage.setItem('INnameStore',e);//
			},
			radio(e){
				localStorage.setItem('radioStore',e);//
			},
			INage(e){
				localStorage.setItem('INageStore',e);//
			},
			INtel(e){
				localStorage.setItem('INtelStore',e);//
			}
		},
		mounted() {
			this.JGajaxlist();
			// 
			if (localStorage.getItem('INnameStore')=='' || localStorage.getItem('INnameStore')==null || localStorage.getItem('INnameStore')==undefined) {}else{this.INname = localStorage.getItem('INnameStore')}
			if (localStorage.getItem('radioStore')=='' || localStorage.getItem('radioStore')==null || localStorage.getItem('radioStore')==undefined) {}else{this.radio = localStorage.getItem('radioStore')}
			if (localStorage.getItem('INageStore')=='' || localStorage.getItem('INageStore')==null || localStorage.getItem('INageStore')==undefined) {}else{this.INage = localStorage.getItem('INageStore')}
			if (localStorage.getItem('INtelStore')=='' || localStorage.getItem('INtelStore')==null || localStorage.getItem('INtelStore')==undefined) {}else{this.INtel = localStorage.getItem('INtelStore')}
		}
	};
</script>

<style scoped>
	/*  */
	.inOrderwtoqx{
		overflow: hidden;
	}
	.inOrderwtoqx span{
		font-size: 12px;
		display: block;
		float: right;
		background-color: #CCCCCC;
		height:30px;
		padding: 0 10px;
		border-radius: 20px;
		line-height: 30px;
	}
	/*  */
	.inOrder {
		padding: 14px;
		width: calc(100% - 28px);
	}

	.inOrderOne {
		background-color: #FFFFFF;
		border-radius: 12px;
		padding: 20px;
		margin-bottom: 14px;
	}

	.inOrderOneTIT {
		font-size: 14px;
		margin-bottom: 16px;
		text-align: left;
		overflow: hidden;
		color: #333333;
	}

	.inOrderOneTIT span {
		float: right;
		color: #D42E00;
	}

	.inOrderOnemud {
		font-size: 12px;
		color: #ccc;
		text-align: left;
	}

	.inOrderOnemud span {
		color: #E14901;
	}

	/*  */
	.inOrderwto {
		background-color: #FFFFFF;
		border-radius: 12px;
		padding: 20px;
		margin-bottom: 14px;
	}

	.INbox {
		overflow: hidden;
		border-bottom: 1px solid #ebedf0;
	}

	.boxxz {
		float: left;
		margin: 12px 18px;
		font-size: 14px;
	}
	/*  */
	.inOrderthree {
		background-color: #FFFFFF;
		border-radius: 12px;
		padding: 20px;
		margin-bottom: 14px;
		text-align: left;
		font-size: 0.875rem;
	}
	.inOrderthree div{
		margin: 10px 0;
	}
	.inOrderthreeright{
		float: right;
	}
	.inOrderthreebut{
		text-align: center;
		line-height: 2rem;
		margin-top: 1.25rem;
		color: #E14901;
		margin-bottom: 0;
	}
	/*  */
	.inOrderBottom {
		width: 100%;
		height: 54px;
		background: #FFFFFF;
		position: fixed;
		bottom: 0;
		left: 0;
		color: #fff;
	}

	.inOrderBottom p {
		width: 80%;
		height: 44px;
		margin: 5px auto;
		background: linear-gradient(90deg, rgba(255, 160, 45, 1) 0%, rgba(255, 103, 20, 1) 100%);
		line-height: 44px;
		border-radius: 44px;
	}
	
</style>



