<template>
  <div id="app">
	   <transition :name="transitionName"> 
	      <!-- <keep-alive> -->
             <router-view/><!--  -->
		  <!-- </keep-alive> -->
	   </transition>   
  </div>
</template>

<script>
import {commonShare, shareTitle, shareUrl, shareImg, shareDesc} from "./USERJS/share";
//
import wx from 'weixin-js-sdk'
export default {
	data() {
	  return {
		transitionName:''
	  };
	},
	created () {
		//
	    // this.getCode();
		//
		this.getMyLocation();
	},
  methods:{
		//
		getCode () { // 
			if (localStorage.getItem('INcodeuid')==undefined || localStorage.getItem('INcodeuid')==null ||localStorage.getItem('INcodeuid')=='') {
				const code = this.$uerstore.getUrlParam("code") // codecodeopenId
				const local = window.location.href
				if (code == null || code === '') {
					window.location.href = 'https://open.weixin.qq.com/connect/oauth2/authorize?appid=' + 'wxf47f6fd95e94366e' + '&redirect_uri=' + encodeURIComponent(local) + '&response_type=code&scope=snsapi_userinfo&state=1#wechat_redirect'
				} else {
					this.getOpenId(code) //code
				}
			}else{
				//
				let YZUID = {
					id:localStorage.getItem('INcodeuid')
				};
				this.axios.post('verId',this.qs.stringify(YZUID)).then((response)=>{
				    // ...	
					//
					if (response.data.code!=1000) {
						//
						localStorage.removeItem('INcodeuid');
						window.location.href = 'https://yxhd.insooner.com/dist';
					}else{
						//
						this.INpagethis();
						commonShare(' ','https://yxhd.insooner.com/dist?urluid='+localStorage.getItem('INcodeuid'),'https://yinxuejiaoyu.oss-cn-hangzhou.aliyuncs.com/images/80.jpg','4800');
					}	
				})
			}
		},
		getOpenId (code) { // code openId/api/user/wechat/login 
			let _this = this
			let thisdatacode = {
				code:code
			};
			this.axios.post('vx',this.qs.stringify(thisdatacode)).then((response)=>{
			    // ...	
				//
				if (response.data.code!=1000) {
					//
					window.location.href = 'https://yxhd.insooner.com/dist';
				}else{
					localStorage.setItem('INcodeuid',response.data.data.id);
					//
					this.INpagethis();
					_this.$uerstore.store.commit("GZHcode",response.data.data.type);//
					console.log(response);
					commonShare(' ','https://yxhd.insooner.com/dist?urluid='+response.data.data.id,'https://yinxuejiaoyu.oss-cn-hangzhou.aliyuncs.com/images/80.jpg','4800');
				}	
			});
		},
		//
		getMyLocation() {
			const that =this;
			//
			const latng = this.$route.query.latng;
			const addr = this.$route.query.city;//
			// console.log(this.$route.query);
			if (latng==null || latng=='' || latng==undefined) {
				// 
				that.getMyLocationTX();
			}else{
				// 
				let latngZH = [];
				latngZH = latng.split(",");
				localStorage.setItem('INlatitude',latngZH[0]);//
				localStorage.setItem('INlongitude',latngZH[1]);//
				that.$uerstore.store.commit("addname",addr);//
			}	 
		},
		//
		getMyLocationTX() {
			var geolocation = new qq.maps.Geolocation("AZ6BZ-G5WWO-UGOWQ-STA4N-NQG72-2TB4X", "");
			geolocation.getLocation(this.showPosition, this.showErr,this.addstate);
		},
		showPosition(position) {
			localStorage.setItem('INlatitude',position.lat);//
			localStorage.setItem('INlongitude',position.lng);//
			//
			this.$uerstore.store.commit("addname",position.city+position.addr);//
		},
		showErr() {
			console.log("");
			localStorage.setItem('INlatitude','30.230001');//
			localStorage.setItem('INlongitude','120.213776');//
			//
			this.$uerstore.store.commit("addname",'');//
		},
		//
		INpagethis(){
			let pageurl = this.$route.query.INpagethisid;
			if (pageurl==1) {
				//
				this.$router.push({path:'/JGorder',query:{}});
			};
		}
	},
	watch: {//watch $router
	    $route(to, from) {
	      //tofrom,,
	      if(to.meta.index > from.meta.index){
	        //
	        this.transitionName = 'slide-left';
	      }else{
	        this.transitionName = 'slide-right';
	      };
	    }
	  },
	mounted(){
	}
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  width: 100%;
  height: 100%;
}
body,html{
	width: 100%;
	height: 100%;
	background-color: rgba(255,255,255,1);
}
.slide-right-enter-active,
.slide-right-leave-active,
.slide-left-enter-active,
.slide-left-leave-active {
  will-change: transform;
  transition: all 500ms;
  position: absolute;
}
.slide-right-enter {
  opacity: 0;
  transform: translate3d(-100%, 0, 0);
}
.slide-right-leave-active {
  opacity: 0;
  transform: translate3d(100%, 0, 0);
}
.slide-left-enter {
  opacity: 0;
  transform: translate3d(100%, 0, 0);
}
.slide-left-leave-active {
  opacity: 0;
  transform: translate3d(-100%, 0, 0);
}
</style>



